import { Component, OnInit } from '@angular/core';
import { environment } from 'src/environments/environment';
import { NgxSpinnerService } from 'ngx-spinner';
import { ServerRepoService } from 'src/app/services/server-repo.service';
import { AppComponent } from 'src/app/app.component';
import { CookieService } from 'ngx-cookie-service';

@Component({
    selector: 'app-wallet',
    templateUrl: './wallet.component.html',
    styleUrls: ['./wallet.component.css']
})
export class WalletComponent implements OnInit {
    regexForEightChar = (/^(\d+)?([*]?\d{0,8})?$/);
    withdrawCoinArr = [];
    depositCoinArr = [];
    depositSecObj = {coin:"", balance:"", address:""};
    withdrawSecObj = {coin:"", balance:"", address:"", amount:"", fees:"", checkboxStatus: false};

    constructor(private server: ServerRepoService, private appC: AppComponent, private spinner: NgxSpinnerService, private cookieService: CookieService) { }

    ngOnInit() {
        this.startLoader();
        this.getCoinList();
    }

    /** Function for start loader */
    startLoader() {
        this.spinner.show();
        setTimeout(() => {
            this.spinner.hide();
        },3000);
    }

    /** Function to get coin list */
    getCoinList() {
        this.server.getMethod(environment.baseUrl+environment.wallet.version,"wallet/coin/get-coin-list").subscribe((succ) => {
            if(succ.status == 200) {
                this.withdrawCoinArr = succ.data;
                this.depositCoinArr = succ.data;
                if(this.depositCoinArr.length) {
                    this.depositSecObj.coin = this.depositCoinArr[0].coinShortName;
                    this.getBalance(this.depositSecObj.coin,"deposit");
                    this.getAddress();
                }
                if(this.withdrawCoinArr.length) {
                    this.withdrawSecObj.coin = this.withdrawCoinArr[0].coinShortName;
                    this.withdrawSecObj.fees = this.withdrawCoinArr[0].withdrawlFee + this.withdrawSecObj.coin;
                    this.getBalance(this.withdrawSecObj.coin,"withdraw");
                }
            } else {
                this.withdrawCoinArr = [];
                this.depositCoinArr = [];
                this.appC.showErrorAlert(succ.message);
            }
        }, (err) => {
            this.withdrawCoinArr = [];
            this.depositCoinArr = [];
            this.appC.showErrorAlert(err.error.message);
        });
    }

    /** Function for get balance of selected coin */
    getBalance(coin,type) {
        this.server.getMethod(environment.baseUrl+environment.wallet.version,"wallet/wallet/get-balance?coinName="+coin).subscribe((succ) => {
            if(succ.status == 200) {
                switch(type) {
                    case "deposit":
                        this.depositSecObj.balance = succ.data.walletBalance;
                        break;
                    case "withdraw":
                        this.withdrawSecObj.balance = succ.data.walletBalance;
                        break;
                }
            } else {
                switch(type) {
                    case "deposit":
                        this.depositSecObj.balance = "0";
                        break;
                    case "withdraw":
                        this.withdrawSecObj.balance = "0";
                        break;
                }
                this.appC.showErrorAlert(succ.message);
            }
        }, (err) => {
            this.appC.showErrorAlert(err.error.message);
        });
    }

    /** Function for get coin address */
    getAddress() {
        this.server.getMethod(environment.baseUrl+environment.wallet.version,"wallet/wallet/get-address?coinName="+this.depositSecObj.coin).subscribe((succ) => {
            if(succ.status == 200) {
                this.depositSecObj.address = succ.data.walletAddress;
            } else {                
                this.depositSecObj.address = "";
                this.appC.showErrorAlert(succ.message);
            }
        }, (err) => {
            this.depositSecObj.address = "";
            this.appC.showErrorAlert(err.error.message);
        });
    }

    /** Function for copy data */
    copyToClipboard(data) {
        this.appC.showInfoAlert("Coin address has been copied to clipboard.");
        let selBox = document.createElement('textarea');
        selBox.style.position = 'fixed';
        selBox.style.left = '0';
        selBox.style.top = '0';
        selBox.style.opacity = '0';
        selBox.value = data;
        document.body.appendChild(selBox);
        selBox.focus();
        selBox.select();
        document.execCommand('copy');
        document.body.removeChild(selBox);
    }

    /** Function for deposit coin change */
    depositCoinChangeFunc() {
        this.getBalance(this.depositSecObj.coin,"deposit");
        this.getAddress();
    }

    /** Function for generate wallet address */
    generateAddress() {
        if(this.depositSecObj.address) {
            this.appC.showInfoAlert("Address is already generated.");
            return;
        } else {
            this.getAddress();
        }
    }

    /** Function for withdraw coin change */
    withdrawCoinChangeFunc() {
        this.getBalance(this.withdrawSecObj.coin,"withdraw");
        let ind = this.withdrawCoinArr.findIndex(e => e.coinShortName == this.withdrawSecObj.coin);
        if(ind != -1) {
            this.withdrawSecObj.fees = this.withdrawCoinArr[ind].withdrawlFee + this.withdrawSecObj.coin;
        }
        this.clearWithdrawFields();
    }

    /** Function for clear withdraw fields */
    clearWithdrawFields() {
        this.withdrawSecObj.address = "";
        this.withdrawSecObj.amount = "";
        this.withdrawSecObj.checkboxStatus = false;
    }

    /** Function to restrict space */
    restrictSpace(event) {
        var k = event.charCode;
        if (k === 32) return false;
    }

    /** Function to restrict character */
    restrictChar(event) {
        var k = event.charCode;
        if (event.key === 'Backspace')
            k = 8;
        if (k >= 48 && k <= 57 || k == 8 || k == 46)
            return true;
        else
            return false;    
    }

    /** Function to restrict length after dot */
    restrictLength(type) {
        switch(type) {
            case 'wa':
                if(this.withdrawSecObj.amount.includes(".")) {
                    if (!this.regexForEightChar.test(this.withdrawSecObj.amount)) {
                        let tempVal = this.withdrawSecObj.amount.split('.');
                        this.withdrawSecObj.amount = tempVal[0] + '.' + tempVal[1].slice(0, 8);
                    }
                }
                break;
        }
    }

    /** Function for submit withdraw */
    submitWithdraw() {
        if(this.withdrawSecObj.address.trim().length == 0) {
            this.appC.showErrorAlert("Please enter wallet address.");
            return;
        } else if(this.withdrawSecObj.amount.trim().length == 0) {
            this.appC.showErrorAlert("Please enter amount.");
            return;
        } else if(Number(this.withdrawSecObj.amount) <= 0 || this.withdrawSecObj.amount == ".") {
            this.appC.showErrorAlert("Please enter valid amount.");
            return;
        } else if(!this.withdrawSecObj.checkboxStatus) {
            this.appC.showErrorAlert("Please accept withdraw condition.");
            return;
        }  else {
            console.log("data -> ",this.withdrawSecObj);
        }
    }
}
