import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HeaderComponent } from './modules/header/header/header.component';
import { HomeComponent } from './modules/home/home/home.component';
import { LoginComponent } from './modules/login/login/login.component';
import { AuthGuard } from './security/auth.guard';
import { SignupComponent } from './modules/signup/signup/signup.component';
import { ExchangeComponent } from './modules/exchange/exchange/exchange.component';
import { ContactComponent } from './modules/contact/contact/contact.component';
import { WalletComponent } from './modules/wallet/wallet/wallet.component';
import { SuggestionComponent } from './modules/suggestion/suggestion/suggestion.component';
import { PagenotfoundComponent } from './modules/pagenotfound/pagenotfound/pagenotfound.component';
import { EmailverifyComponent } from './modules/emailverify/emailverify/emailverify.component';
import { ResendemailComponent } from './modules/resendemail/resendemail/resendemail.component';
import { SettingComponent } from './modules/setting/setting/setting.component';
import { MessageComponent } from './modules/message/message/message.component';

const routes: Routes = [
    {
        path:'', component: HeaderComponent,
        children: [
            {path:'', component:HomeComponent},
            {path:'login', component:LoginComponent, canActivate: [AuthGuard]},
            {path:'signup', component:SignupComponent, canActivate: [AuthGuard]},
            {path:'exchange', component:ExchangeComponent, canActivate: [AuthGuard]},
            {path:'about', component:ContactComponent, canActivate: [AuthGuard]},
            {path:'wallet_deposit', component:WalletComponent, canActivate: [AuthGuard]},
            {path:'wallet_withdraw', component:WalletComponent, canActivate: [AuthGuard]},
            {path:'suggestion', component:SuggestionComponent, canActivate: [AuthGuard]},
            {path:'setting', component:SettingComponent, canActivate: [AuthGuard]},
            {path:'message', component:MessageComponent, canActivate: [AuthGuard]},
            {path:'emailVerify', component:EmailverifyComponent},
            {path:'resendEmail', component:ResendemailComponent},
            { path: '**', component: PagenotfoundComponent}
        ] 
    }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }
