    /** Function for manage socket event */
    updateSocketEvent(mode) {
        let data = {
            "request": "",
            "symbol": "",
            "depth": 50
        }
        switch(mode) {
            case 1:
                if(this.server.wsObj.readyState) {
                    /** Code for subscribe ticker data */
                    // data.request = "subscribeTicker";
                    // data.symbol = this.currCoinPairObj.coin+"_"+this.currCoinSelectedObj.coin;
                    // this.server.wsObj.send(JSON.stringify(data));

                    /** Code for subscribe order book data */
                    data.request = "subscribeOrderBook";
                    data.symbol = this.currCoinPairObj.coin+"_"+this.currCoinSelectedObj.coin;
                    this.server.wsObj.send(JSON.stringify(data));

                    /** Code for subscribe trade history data */
                    data.request = "subscribeTradeHistory";
                    data.symbol = this.currCoinPairObj.coin+"_"+this.currCoinSelectedObj.coin;
                    this.server.wsObj.send(JSON.stringify(data));   
                }               

                break;
            case 2: 
                if(this.server.wsObj.readyState) {
                    /** Code for unsubscribe ticker data */
                    // data.request = "unsubscribeTicker";
                    // data.symbol = this.currCoinPairObj.coin+"_"+this.currCoinSelectedObj.coin;
                    // this.server.wsObj.send(JSON.stringify(data));

                    /** Code for unsubscribe order book data */
                    data.request = "unsubscribeOrderBook";
                    data.symbol = this.currCoinPairObj.coin+"_"+this.currCoinSelectedObj.coin;
                    this.server.wsObj.send(JSON.stringify(data));

                    /** Code for unsubscribe trade history data */
                    data.request = "unsubscribeTradeHistory";
                    data.symbol = this.currCoinPairObj.coin+"_"+this.currCoinSelectedObj.coin;
                    this.server.wsObj.send(JSON.stringify(data));       
                }               

                break;
        }
    }

    /** Function for change user coin balance  */
    userBalCoinChange() {
        let ind = this.userBalArr.findIndex(obj => obj.coin == this.balSecObj.coin);
        if(ind != -1) {
            this.balSecObj.balance = this.userBalArr[ind].balance;
        }
    }

    /** Function for change string into number */
    changeToNumberType(data) {
        return Number(data);
    }

    /** Function for get user's open order */
    getOpenOrder() {
        this.server.getMethod(environment.baseUrl+environment.order.version,"trade/my-active-orders?symbol="+this.currCoinPairObj.coin+"_"+this.currCoinSelectedObj.coin).subscribe((succ) => {
            if(succ.status == 200) {
                this.openOrderArr = [];
                succ.data.forEach((obj) => {
                    this.openOrderArr.push({
                        orderId: obj.orderId,
                        orderTime: new Date(obj.creationTime).getTime(),
                        pair: obj.instrument.replace("_","/"),
                        orderType: obj.orderSide,
                        price: obj.limitPrice,
                        amount: obj.quantity,
                        happening: ((obj.quantity - obj.currentQuantity)/obj.quantity)*100,
                        total: (obj.limitPrice * obj.quantity).toFixed(6),
                        click: false
                    });
                });
                this.openOrderArr = this.sortData(this.openOrderArr);
            } else {
                this.openOrderArr = [];
                this.appC.showErrorAlert(succ.message);
            }
        }, (err) => {
            this.openOrderArr = [];
            this.appC.showErrorAlert(err.error.message);
        });
    }

    /** Function for get user's order history */
    getOrderHistory() {
        this.server.getMethod(environment.baseUrl+environment.order.version,"trade/my-order-history?symbol="+this.currCoinPairObj.coin+"_"+this.currCoinSelectedObj.coin).subscribe((succ) => {
            if(succ.status == 200) {
                this.orderHistoryArr = [];
                succ.data.forEach(obj => {
                    this.orderHistoryArr.push({
                        orderTime: obj.orderStatus == "CANCELLED" ? new Date(obj.creationTime).getTime() : new Date(obj.lastExecutionTime).getTime(),
                        pair: obj.instrument.replace("_","/"),
                        orderType: obj.orderSide,
                        price: obj.limitPrice,
                        amount: obj.quantity,
                        orderStatus: obj.orderStatus,
                        happening: ((obj.quantity - obj.currentQuantity)/obj.quantity)*100,
                        total: obj.orderStatus == "CANCELLED" ? obj.limitPrice * obj.quantity : obj.quantity * obj.avgExecutionPrice,

                    });
                });
                this.orderHistoryArr = this.sortData(this.orderHistoryArr);
            } else {
                this.orderHistoryArr = [];
                this.appC.showErrorAlert(succ.message);
            }
        }, (err) => {
            this.orderHistoryArr = [];
            this.appC.showErrorAlert(err.error.message);
        });
    }

    /** Function for sort data */
    sortData(arr) {
        arr.sort((a,b) => b.orderTime - a.orderTime);
        return arr;
    }

    /** Function for cancel order */
    orderCancelFunc(obj) {
        obj.click = true;
        let data = {
            "orderId": obj.orderId,
            "symbol": obj.pair.replace("/","_")
        }
        this.spinner.show();
        this.server.postMethod(data,environment.baseUrl+environment.order.version,"trade/cancel-order").subscribe((succ) => {
            this.spinner.hide();
            if(succ.status == 200) {
                this.appC.showSuccessAlert("Order cancelled successfully.");
                this.getOpenOrder();
                this.getUserBaseCoinBalance();
                this.getUserPairCoinBalance();
            } else {
                this.appC.showErrorAlert(succ.message);
                obj.click = false;
            }
        }, (err) => {
            this.spinner.hide();
            this.appC.showErrorAlert(err.error.message);
            obj.click = false;
        });
    }

    /** Function for stop flicker on order book */
    stopFlicker(type) {
        switch(type) {
            case 1:
                setTimeout(() => {
                    this.buyOrderArr.forEach(obj => {
                        obj.flicker = false;
                    });
                }, 500);
                break;
            case 2:
                setTimeout(() => {
                    this.sellOrderArr.forEach(obj => {
                        obj.flicker = false;
                    });
                }, 500);
                break;
        }
    }

    /** Function for depth chart timer */
    startTimerForDepthChart() {        
        this.depthChartTimerID = setInterval(() => {
            this.drawDepthChart();
        },10000);
    }
