import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { AppComponent } from 'src/app/app.component';
import { ServerRepoService } from 'src/app/services/server-repo.service';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';

@Component({
    selector: 'app-signup',
    templateUrl: './signup.component.html',
    styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {
    emailRegex = (/^[A-Z0-9_-]+([\.][A-Z0-9_-]+)*@[A-Z0-9-]+(\.[a-zA-Z]{2,4})+$/i);
    passRegex = (/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/i);
    signupObj = {email:"", password:"", cnfPassword:"", state:"France", recaptcha:"", ppCheckbox:false, tcCheckbox:false, responsCheckbox: false};

    constructor(public router: Router, private spinner: NgxSpinnerService, private appC: AppComponent, private service: ServerRepoService) { }

    ngOnInit() {
        window.scrollTo(0,0);
        this.fillFields();
    }

    /** Function for auto fill of fields */
    fillFields() {
        this.spinner.show();
        setTimeout(() => {
            this.spinner.hide();
            // this.appC.showInfoAlert("One moment we're making cool stuff happen...");
            setTimeout(() => {
                this.signupObj.email = (Math.floor(Math.random()*90000) + 10000)+"blockswap@gmail.com";
                this.signupObj.password = "qwerty@1";
                this.signupObj.cnfPassword = "qwerty@1";
                this.signupObj.ppCheckbox = true;
                this.signupObj.tcCheckbox = true;
                this.signupObj.responsCheckbox = true;
                this.service.autoFilled = true;
                this.service.autoFilledObj.email = this.signupObj.email;
                this.service.autoFilledObj.password = this.signupObj.password;
                this.signupFunc();
            },2000);            
        },2000);        
    }

    /** Function for google recaptcha click */
    resolved(e) {
        if(e)
            this.signupObj.recaptcha = e;
        else {
            this.signupObj.recaptcha = "";
        }
    }

    /** Function for signup */
    signupFunc() {
        if(this.signupObj.email.trim().length == 0) {
            this.appC.showErrorAlert("Please enter your email address.");
            return;
        } else if(!this.emailRegex.test(this.signupObj.email)) {
            this.appC.showErrorAlert("Please enter valid email address.");
            return;
        } else if(this.signupObj.password.trim().length == 0) {
            this.appC.showErrorAlert("Please enter your password.");
            return;
        } else if(!this.passRegex.test(this.signupObj.password)) {
            this.appC.showErrorAlert("Password must be 8 or more characters and it should contain at least one special character, one alphabet and one digit.");
            return;
        } else if(this.signupObj.cnfPassword.trim().length == 0) {
            this.appC.showErrorAlert("Please enter confirm password.");
            return;
        } else if(this.signupObj.password != this.signupObj.cnfPassword) {
            this.appC.showErrorAlert("Password doesn't match.");
            return;
        } /*else if(this.signupObj.recaptcha == "") {
            this.appC.showErrorAlert("Please check that you are not a robot.");
            return;
        }*/ else if(!this.signupObj.ppCheckbox) {
            this.appC.showErrorAlert("Please accept blockswap privacy policy.");
            return;
        } else if(!this.signupObj.tcCheckbox) {
            this.appC.showErrorAlert("Please accept blockswap terms & conditions.");
            return;
        } else if(!this.signupObj.responsCheckbox) {
            this.appC.showErrorAlert("Please accept full responsbility checkbox.");
            return;
        } else {            
            let data = {      
                "email": this.signupObj.email,
                "password": this.signupObj.password,
                "country": this.signupObj.state,
                "roleStatus": "USER",
                "webUrl": environment.websiteUrl+"/emailVerify"
            }
            this.spinner.show();
            this.service.postMethod(data,environment.baseUrl+environment.user.version,"user/signup").subscribe((succ) => {
                this.spinner.hide();
                if(succ.status == 200) {
                    this.appC.showSuccessAlert("Your account created successfully. Now you will be redirect to login page.");
                    this.router.navigate(['login']);
                } else {    
                    this.appC.showErrorAlert(succ.message);
                }
            }, (err) => {
                this.spinner.hide();
                this.appC.showErrorAlert(err.error.message);
            });
        }
    }
}
