import { Component, OnInit } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';
import { Router } from '@angular/router';
import { HeaderComponent } from 'src/app/modules/header/header/header.component';
import { ServerRepoService } from 'src/app/services/server-repo.service';
import { environment } from 'src/environments/environment';
import { AppComponent } from 'src/app/app.component';

declare var $: any;

@Component({
    selector: 'app-home',
    templateUrl: './home.component.html',
    styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
    loginStatus = false;
    coinListObj = {coin:""};
    coinArr = [];
    coinPairArr = [];
    sliderObj = {base:"", exe:"", price:"", high:"", low:"", volume:""};

    constructor(public router: Router, private cookieService: CookieService, private header: HeaderComponent, private server: ServerRepoService, private appC: AppComponent) {
        this.header.fireToChild().subscribe(msg => {
            msg.text=="logout" ? this.checkLoginStatus() : "";
        });

        var self = this;
        /** Listener for socket connection */
        this.server.wsObj.addEventListener('message', function (event) {
            let data = JSON.parse(event.data);
            if(data.data) {
                switch(data.responseType) {
                    case "tickerUpdate":
                        self.sliderObj.price = data.data.lastPrice ? Number(data.data.lastPrice).toFixed(4) : "0.0000";
                        self.sliderObj.volume = data.data.totalVolume ? Number(data.data.totalVolume).toFixed(4) : "0.0000";
                        self.sliderObj.high = data.data.highest24HourPrice ? Number(data.data.highest24HourPrice).toFixed(4) : "0.0000";
                        self.sliderObj.low = data.data.lowest24HourPrice ? Number(data.data.lowest24HourPrice).toFixed(4) : "0.0000";

                        let pairArr = self.coinPairArr.filter(obj => obj.pair.replace("/","_") == data.symbol);
                        if(pairArr.length) {
                            if(pairArr[0].price != Number(data.data.lastPrice).toFixed(4)) {
                                pairArr[0].flicker = true;
                                self.stopFlicker();
                            }
                            pairArr[0].price = data.data.lastPrice ? Number(data.data.lastPrice).toFixed(4) : "0.0000";
                            pairArr[0].high = data.data.highest24HourPrice ? Number(data.data.highest24HourPrice).toFixed(4) : "0.0000";
                            pairArr[0].low = data.data.lowest24HourPrice ? Number(data.data.lowest24HourPrice).toFixed(4) : "0.0000";
                            pairArr[0].volume = data.data.totalVolume ? Number(data.data.totalVolume).toFixed(4) : "0.0000";                            
                        }
                        break;
                }
            }
        });

        /** Listener for liquidity socket connection */
        /*this.server.liqWSObj.addEventListener('message', function (event) {
            let data = JSON.parse(event.data); 
            if(data.params) {           
                switch(data.method) {
                    case "ticker": 
                        self.sliderObj.price = data.params.last;
                        self.sliderObj.high = data.params.high;
                        self.sliderObj.low = data.params.low;
                        self.sliderObj.volume = data.params.volume;

                        let pairArr = self.coinPairArr.filter(obj => obj.pair.replace("/","") == data.params.symbol);
                        if(pairArr.length) {
                            pairArr[0].price = data.params.last;
                            pairArr[0].high = data.params.high;
                            pairArr[0].low = data.params.low;
                            pairArr[0].volume = data.params.volume;
                            pairArr[0].flicker = true;
                            self.stopFlicker();
                        }
                        break;
                }
            }
            if(data.error) {
                if(data.error.code == 2001) {
                    self.sliderObj.price = "0.0000";
                    self.sliderObj.high = "0.0000";
                    self.sliderObj.low = "0.0000";
                    self.sliderObj.volume = "0.0000";
                }
            }
        });*/
    }

    ngOnInit() {
        window.scrollTo(0,0);
        this.setSlickContent();
        this.checkLoginStatus();
        this.getCoinList();
    }

    /** Function for set slick content */
    setSlickContent() {
        $('.chartSlider').slick({
            dots: false,
            arrows: false,  
            centerMode: true,
            centerPadding: '10px',
            slidesToShow: 3,
            slidesToScroll: 1,
            autoplay: true,
            autoplaySpeed: 2500,
            responsive: [{
                breakpoint: 768,
                settings: {
                    arrows: false,
                    centerMode: true,
                    slidesToShow: 3
                }
            },{
                breakpoint: 480,
                settings: {
                    arrows: false,
                    centerMode: true,
                    /*   centerPadding: '40px', */
                    slidesToShow: 1
                }
            }]
        });
    }

    /** Function to check login status */
    checkLoginStatus() {
        if(this.cookieService.get("token")) {
            this.loginStatus = true;
        } else {
            this.loginStatus = false;
        }
    }

    /** Function for navigate to page */
    goToPage(type) {
        switch(type) {
            case 1:
                this.router.navigate(['signup']);
                break;
        }
    }

    /** Function for get coin list */
    getCoinList() {
        this.server.getMethod(environment.baseUrl+environment.wallet.version,"wallet/coin/get-symbol-list").subscribe((succ) => {
            // console.log("succ -> ", JSON.stringify(succ));
            if(succ.status == 200) {
                this.coinArr = [];
                this.coinPairArr = [];
                succ.data.forEach(obj => {
                    this.coinArr.push({
                        coin: obj.baseCoin
                    });
                    this.coinPairArr.push({
                        baseCoin: obj.baseCoin,
                        exeCoin: obj.executableCoin,
                        pair: obj.executableCoin+"/"+obj.baseCoin,
                        price: "0.0000",
                        high: "0.0000",
                        low: "0.0000",
                        volume: "0.0000",
                        selected: false,
                        flicker: false
                    });
                });
                if(this.coinArr.length) {
                    this.coinListObj.coin = this.coinArr[0].coin;
                }
                if(this.coinPairArr.length) {
                    this.coinPairArr[0].selected = true;
                    this.sliderObj.base = this.coinPairArr[0].baseCoin;
                    this.sliderObj.exe = this.coinPairArr[0].exeCoin;
                    this.sliderObj.price = this.coinPairArr[0].price;
                    this.sliderObj.high = this.coinPairArr[0].high;
                    this.sliderObj.low = this.coinPairArr[0].low;
                    this.sliderObj.volume = this.coinPairArr[0].volume;
                }
            } else {
                this.coinArr = [];
                this.coinPairArr = [];
                this.appC.showErrorAlert(succ.message);
            }
        }, (err) => {
            this.appC.showErrorAlert(err.error.message);
        });
    }

    /** Function for select pair */
    selectPair(data) {
        data.selected = true;
        this.sliderObj.base = data.baseCoin;
        this.sliderObj.exe = data.exeCoin;
        this.sliderObj.price = data.price;
        this.sliderObj.high = data.high;
        this.sliderObj.low = data.low;
        this.sliderObj.volume = data.volume;
        this.coinPairArr.forEach((obj) => {
            if(data.exeCoin != obj.exeCoin) {
                obj.selected = false;
            }
        });
        if(this.cookieService.get("token")) {
            this.router.navigate(['exchange']);
        }
    }

    stopFlicker() {
        setTimeout(() => {
            this.coinPairArr.forEach(obj => {
                obj.flicker = false;
            });
        },500);
    }
}
