import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { AppComponent } from 'src/app/app.component';
import { Router } from '@angular/router';
import { HeaderComponent } from '../../header/header/header.component';
import { CookieService } from 'ngx-cookie-service';
import { ServerRepoService } from 'src/app/services/server-repo.service';
import { environment } from 'src/environments/environment';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
    loginObj = {email:"", password:""};
    emailRegex = (/^[A-Z0-9_-]+([\.][A-Z0-9_-]+)*@[A-Z0-9-]+(\.[a-zA-Z]{2,4})+$/i);
    token = "";

    constructor(public router: Router, private spinner: NgxSpinnerService, private appC: AppComponent, public header: HeaderComponent, private cookieService: CookieService, private service: ServerRepoService) { }

    ngOnInit() {
        window.scrollTo(0,0);
        this.checkAutoFilled();
    }

    ngOnDestroy() {
        this.service.autoFilled = false;
        this.service.autoFilledObj.email = "";
        this.service.autoFilledObj.password = "";
    }

    /** Function for check auto filled data */
    checkAutoFilled() {
        if(this.service.autoFilled) {            
            setTimeout(() => {       
                this.appC.showInfoAlert("Please wait we are preparing the service for you.");         
                setTimeout(() => {
                    this.loginObj.email = this.service.autoFilledObj.email;
                    this.loginObj.password = this.service.autoFilledObj.password;
                    this.loginFunc();
                },3000);            
            },3000);
        }
    }

    /** Function for login */
    loginFunc() {
        if(this.loginObj.email.trim().length == 0) {
            this.appC.showErrorAlert("Please enter your email address.");
            return;
        } else if(!this.emailRegex.test(this.loginObj.email)) {
            this.appC.showErrorAlert("Please enter valid email address.");
            return;
        } else if(this.loginObj.password.trim().length == 0) {
            this.appC.showErrorAlert("Please enter your password.");
            return;
        } else {
            let data = {
                "email": this.loginObj.email,
                "password": this.loginObj.password
            }
            this.spinner.show();
            this.service.postMethod(data,environment.baseUrl+environment.auth.version,"auth").subscribe((succ) => {
                this.spinner.hide();
                if(succ.status == 200) {
                    this.cookieService.set("token",succ.data.token);
                    this.getUserDetails();                    
                } else {    
                    this.appC.showErrorAlert(succ.message);
                }
            }, (err) => {
                this.spinner.hide();
                this.appC.showErrorAlert(err.error.message);
            });
        }
    }

    /** Function for get user details */
    getUserDetails() {
        this.service.getMethod(environment.baseUrl+environment.user.version,"user/my-account").subscribe((succ) => {
            if(succ.status == 200) {
                this.cookieService.set("userId",succ.data.userId);
                this.header.checkLogin();
                this.router.navigate(['exchange']);
            } else {
                this.cookieService.delete("token");
                this.appC.showErrorAlert(succ.message);
            }
        }, (err) => {
            this.cookieService.delete("token");
            this.appC.showErrorAlert(err.error.message);
        });
    }
}
