import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { AppComponent } from 'src/app/app.component';
import { Router } from '@angular/router';
import { HeaderComponent } from '../../header/header/header.component';
import { CookieService } from 'ngx-cookie-service';

declare var $: any;

@Component({
    selector: 'app-overlay',
    templateUrl: './overlay.component.html',
    styleUrls: ['./overlay.component.css']
})
export class OverlayComponent implements OnInit {
    currBtn = "";

    constructor(public router: Router, private spinner: NgxSpinnerService, private appC: AppComponent, public header: HeaderComponent, private cookieService: CookieService) { }

    ngOnInit() {
        // console.log("over lay called");
        let splitArr = window.location.href.split('/');
        let url = splitArr[splitArr.length -1];
        this.setCurrButton(url);   
    }

    /** Function for set current active button */
    setCurrButton(type) {
        switch(type) {
            case "exchange":
                this.currBtn = "market";
                break;
            case "about":
                this.currBtn = "about";
                break;
            case "wallet_deposit":
                this.currBtn = "deposit";
                break;
            case "wallet_withdraw":
                this.currBtn = "withdraw";
                break;
            case "suggestion":
                this.currBtn = "suggestion";
                break;
            default:
                this.currBtn = "home";
        }
    }

    /** Function for overlay click */
    openOverlay() {
        $(".overlayBox").toggleClass("animate");
    }

    /** Function for navigate to pages */
    goToPage(type) {
        switch(type) {
            case 1:
                this.currBtn = "market";
                this.router.navigate(['exchange']);
                break;
            case 2:
                this.currBtn = "about";
                this.router.navigate(['about']);
                break;
            case 3:
                this.currBtn = "deposit";
                this.router.navigate(['wallet_deposit']);
                break;
            case 4:
                this.currBtn = "withdraw";
                this.router.navigate(['wallet_withdraw']);
                break;
            case 5:
                this.currBtn = "suggestion";
                this.router.navigate(['suggestion']);
                break;
            case 6:
                this.currBtn = "home";
                this.router.navigate(['']);
                break;
        }
    }
}
